import { observer } from 'mobx-react-lite';
import { useLocation } from 'react-router-dom';
import { ChangeGoods } from './component/ChangeGoods/ChangeGoods';
import { GoodsList } from './component/Goods/GoodsList';

export const Setting: React.FC = observer(() => {
  const router = useLocation().pathname;

  return (
    <>
      {router.includes('goods') ? (
        <GoodsList />
      ) : (
        router.includes('item') && <ChangeGoods />
      )}
    </>
  );
});
