import { observer } from 'mobx-react-lite';
import { useEffect, useState } from 'react';
import { FileItemT, FileT } from '../Select/Select.conf';
import { ReactComponent as Plus } from '../../../../../../img/icons/plus.svg';
import { ReactComponent as Closed } from '../../../../../../img/icons/x.svg';
import './ProductFileds.scss';
import { API_URL } from '../../../../../../components/Routers/links';

export const ProductFileds: React.FC<FileT> = observer(
  ({ register, name, getValues, setValue, imgOld, setImgOld }) => {
    const [img, setImg] = useState<File[]>(
      getValues('files')?.length ? getValues('files') : []
    );
    const [imgError, setImgError] = useState<File[]>([]);

    function updateImageDisplay(e: any) {
      const input: File[] = e.target.files;
      let arr: File[] = [];
      const arrErr: File[] = [];

      if (input?.length) {
        for (const [name, item] of Object.entries(input)) {
          if (validFileType(item) && returnFileSize(item.size)) {
            arr.push(item);
          } else {
            arrErr.push(item);
          }
        }
      }

      arr = [...img, ...arr];
      setImg(arr);
      setImgError([...imgError, ...arrErr]);

      const dt = new DataTransfer();
      arr.map((item) => {
        dt.items.add(item);
      });
      return dt.files;
    }

    const handlerDelete = (error: boolean, element: File, old: boolean) => {
      if (error) {
        setImgError(imgError.filter((item) => item.name != element.name));
      } else if (old) {
        const oldelem = Array.from(Object.entries(JSON.parse(imgOld))).filter(
          (item) => {
            return item[0].toString() != element[0].toString();
          }
        );
        setImgOld(JSON.stringify(Object.fromEntries(oldelem)));
      } else {
        const arr = img.filter((item) => item.name != element.name);
        setImg(arr);

        const dt = new DataTransfer();
        arr.map((item) => {
          dt.items.add(item);
        });

        setValue('files', dt.files);
      }
    };

    return (
      <div className="imgfiles">
        <div className="imgfiles__head">
          <div className="titleproductadmin  ">
            <h3>Downloang image</h3>
          </div>
        </div>
        <div className="imgfiles__body">
          <div className="imgfiles__input">
            <label htmlFor="files">
              <p className="mini">Add images, no more 2mb</p>
              <div className="imgfiles__svg">
                <Plus />
              </div>
            </label>
            <input
              type="file"
              {...register('files', {
                onChange: (e) => updateImageDisplay(e),
              })}
              accept=".jpg, .jpeg, .png"
              multiple
            />
          </div>
          <div className="imgfiles__items">
            <div className="imgfiles__preview">
              {Array.from(Object.entries(JSON.parse(imgOld || '{}'))).map(
                (item, index) => {
                  return (
                    <ItemFile
                      item={item}
                      key={index}
                      error={false}
                      old={true}
                      handler={handlerDelete}
                    />
                  );
                }
              )}
              {img.map((item, index) => {
                return (
                  <ItemFile
                    item={item}
                    key={index}
                    error={false}
                    old={false}
                    handler={handlerDelete}
                  />
                );
              })}
              {imgError.map((item, index) => {
                return (
                  <ItemFile
                    item={item}
                    key={index}
                    error={true}
                    old={false}
                    handler={handlerDelete}
                  />
                );
              })}
            </div>
          </div>
        </div>
      </div>
    );
  }
);
export const ItemFile: React.FC<FileItemT> = observer(
  ({ item, error, handler, old }) => {
    return (
      <div className="itemfile">
        <>
          {error ? (
            <div className="itemfile__text">
              <p>
                {'File name ' +
                  item.name +
                  ': Not a valid file type or the file size large 2mb.'}
              </p>
            </div>
          ) : old ? (
            <>
              <div className="itemfile__name">
                <p>{'File name ' + item[0] + '.'}</p>
              </div>
              <div className="itemfile__back">
                <div
                  style={{ backgroundImage: `url(${API_URL}${item[1]})` }}
                  className="itemfile__item"
                ></div>
              </div>
            </>
          ) : (
            <>
              <div className="itemfile__name">
                <p>
                  {`File name 
                    ${item.name}.
                    size:
                    ${returnFileSize(item.size)}.`}
                </p>
              </div>
              <div className="itemfile__back">
                <div
                  style={{
                    backgroundImage: `url(${window.URL.createObjectURL(item)})`,
                  }}
                  className="itemfile__item"
                ></div>
              </div>
            </>
          )}
        </>
        <div
          className="itemfile__delete"
          onClick={() => handler(error, item, old)}
        >
          <Closed />
        </div>
      </div>
    );
  }
);

const fileTypes = ['image/jpeg', 'image/pjpeg', 'image/png'];

function validFileType(file) {
  for (let i = 0; i < fileTypes.length; i++) {
    if (file.type === fileTypes[i]) {
      return true;
    }
  }

  return false;
}

function returnFileSize(number) {
  if (number < 1024) {
    return number + 'bytes';
  } else if (number > 1024 && number < 1048576) {
    return (number / 1024).toFixed(1) + 'KB';
  } else if (number > 1048576 && number <= 2048576) {
    return (number / 1048576).toFixed(1) + 'MB';
  } else {
    return false;
  }
}
