import { observer } from 'mobx-react-lite';
import React, { useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
export const UserHarmonic: React.FC<UserHarmonicTypes> = observer(
  ({ item }) => {
    const router = useLocation().pathname;
    const ref = useRef<HTMLSpanElement>(null);

    useEffect(() => {
      if (ref.current) {
        const target = ref.current;
        const p = +(target.closest('p') as HTMLElement).offsetWidth,
          speed = 130,
          duration = p / speed;
        ref.current.style.transition = `width 0.5s ease 0s`;
      }
    });

    return (
      <>
        <li className={`${router.includes(item.path) && 'add'}`}>
          <Link to={`/${item.parent}/${item.path}`}>
            <p>{item.name}</p>
          </Link>
        </li>
      </>
    );
  }
);

type UserHarmonicTypes = {
  item: ArraUser;
};

interface ArraUser {
  name: string;
  path: string;
  parent: string;
}

export const userArray = [
  { name: 'Profile', path: 'profile', parent: 'user' },
  { name: 'Сhange pass', path: 'pass', parent: 'user' },
];
export const adminArray = [
  { name: 'Add Element', path: 'item', parent: 'admin' },
  { name: 'Goods', path: 'goods', parent: 'admin' },
];
