import { observer } from 'mobx-react-lite';
import React from 'react';
import { $ } from '../../../../components/customeJQ/customeJQ';
import { TypeHarmonic } from '../../../../interface/ui.interface';
import './Harmonic.scss';

export const Harmonic: React.FC<TypeHarmonic> = observer(
  ({ submit, name, children }) => {
    const handlerClick = (e: React.MouseEvent<HTMLElement>) => {
      const target = e.target as HTMLElement;

      if (target.closest('li') as HTMLElement) {
        addClass(target, 'li');
      } else if (target.closest('.harmonic') as HTMLElement) {
        $(target.closest(`.harmonic`) as HTMLElement).toggle('add');
      }
    };

    const addClass = (target: HTMLElement, name: string) => {
      if (!$(target.closest(`${name}`) as HTMLElement).has('add')) {
        $(`${name}`).remove('add');
        $(target.closest(`${name}`) as HTMLElement).toggle('add');
      }
    };

    return (
      <div
        onClick={handlerClick}
        className={`${name} ${submit && 'add'}  harmonic`}
      >
        <div className="harmonic__head">
          <p className="">{name}</p>
          <p className="harmonic__arrow ">{'>'}</p>
        </div>
        <div className="harmonic__blockharmonic">
          <ul className="harmonic__blockemersion">{children}</ul>
        </div>
      </div>
    );
  }
);
