import { useSearchParams } from 'react-router-dom';
import { PaginationConf } from '../../../Shop/Goods/components/Pagination/Pagination.conf';
import './PaginationGoods.scss';

export const PaginationGoods: React.FC<PaginationListT> = ({
  count,
  setPage,
  pageOld,
}): JSX.Element => {
  const getPage = () => {
    const rows = new Map();

    for (let item = 1; item <= Math.ceil(+count / 10); item++) {
      rows.set(
        item,
        <PaginationList key={item} pageOld={pageOld} setPage={setPage}>
          {item.toString()}
        </PaginationList>
      );
    }
    return Array.from(rows.values());
  };

  const pathnamepage = useChangePathname();

  const handleClick = (e: React.MouseEvent<HTMLElement>) => {
    let page = pageOld;

    if (
      (
        (e.target as HTMLElement)?.closest(
          '.paginationline__back'
        ) as HTMLElement
      ).dataset.back
    ) {
      page = +page - 1 + '';
    } else {
      page = +page + 1 + '';
    }

    if (+page >= 1 && +page <= +Math.ceil(+count / 10)) {
      pathnamepage('page', page.toString());
      setPage(page);
      document.documentElement.scrollIntoView({
        block: 'start',
        behavior: 'smooth',
      });
    }
  };

  return (
    <div className="paginationline">
      <div className="paginationline__wrap">
        <div onClick={handleClick} data-back className="paginationline__back">
          <p className="mini">Back</p>
        </div>
        <ul>{getPage()}</ul>
        <div onClick={handleClick} data-next className="paginationline__back">
          <p className="mini">Next</p>
        </div>
      </div>
    </div>
  );
};

const PaginationList: React.FC<PaginationConfListT> = ({
  children,
  setPage,
  pageOld,
}): JSX.Element => {
  const pathnamepage = useChangePathname();
  const handleClick = () => {
    pathnamepage('page', children.toString());
    setPage(children);
    document.documentElement.scrollIntoView({
      block: 'start',
      behavior: 'smooth',
    });
  };

  return (
    <li onClick={handleClick} className={+children == +pageOld ? 'add' : ''}>
      <p className="mini">{children}</p>
    </li>
  );
};

export const useChangePathname = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  return (name: string, item: string) => {
    searchParams.set(name, item);
    setSearchParams(searchParams);
  };
};

export type PaginationListT = {
  setPage: React.Dispatch<React.SetStateAction<string>>;
  count: string;
  pageOld: string;
};

export type PaginationConfListT = Pick<PaginationConf, 'children'> &
  Pick<PaginationListT, 'setPage' | 'pageOld'>;
