import { observer } from 'mobx-react-lite';
import { TextAreaT } from '../Select/Select.conf';
import './TextareItem.scss';

export const TextareItem: React.FC<TextAreaT> = observer(
  ({ register, name }) => {
    return (
      <div className="textareaitem">
        <div className="titleproductadmin">
          <h3>Information</h3>
        </div>
        <div className="textareaitem__input">
          <textarea {...register(name)} />
        </div>
      </div>
    );
  }
);
