import { observer } from 'mobx-react-lite';
import { useContext } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { Context } from '../../../..';
import useLoading from '../../../../Hooks/useLoading';
import { updatepass } from '../../../../http/authAPI';
import { InputPassword } from '../../../../UI/Input/Input';
import Loading, { BtnLoading } from '../../../../UI/Loading/Loading';
import './ChangePass.scss';

export const ChangePass: React.FC = observer(() => {
  const { user } = useContext(Context);
  const {
    register,
    handleSubmit,
    formState: { errors },
    getValues,
    setValue,
    setError,
  } = useForm({ mode: 'onSubmit' });
  const [isLoading, fetching] = useLoading(
    async (data: SubmitHandler<FieldValues>) => {
      const response = await updatepass(data);
      if (response.message == 'Ok') {
        user.setUser = response.user;
      } else if (response.message.includes('Invalid')) {
        setError('passwordold', {
          type: 'custom',
          message: response.message,
        });
      }
    }
  );

  return (
    <div className="changepass">
      <div className="changepass__wrap">
        <form onSubmit={handleSubmit(fetching)}>
          <div className="changepass__input">
            <InputPassword
              req={true}
              title="Old password"
              name="passwordold"
              register={register}
              type="password"
              textFiled="Your old password"
              setValue={setValue}
              errors={errors}
            />

            <InputPassword
              req={true}
              title="New Password"
              name="password"
              register={register}
              type="password"
              textFiled="Your new password"
              setValue={setValue}
              errors={errors}
              getValues={getValues}
            />
            <InputPassword
              req={true}
              name="passTwo"
              title="Password Repeat"
              register={register}
              type="password"
              textFiled="Repeat password"
              setValue={setValue}
              errors={errors}
              getValues={getValues}
              twopass={true}
            />
          </div>
          <div className="changepass__footer">
            <div className="changepass__footer">
              <button type="submit" className="changepass__btn">
                {isLoading ? (
                  <BtnLoading>
                    <Loading />
                  </BtnLoading>
                ) : (
                  <p className="mini">UPDATE</p>
                )}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
});
