import { observer } from 'mobx-react-lite';
import { useEffect, useState } from 'react';
import { useFetching } from '../../../../http/shopAPI';
import useLoading from '../../../../Hooks/useLoading';
import './GoodsList.scss';
import Loading from '../../../../UI/Loading/Loading';
import { useNavigate, useSearchParams } from 'react-router-dom';
import { FieldValues } from 'react-hook-form';
import { ReactComponent as Pen } from '../../../../img/icons/pen.svg';
import { ReactComponent as Basket } from '../../../../img/icons/trash.svg';
import { Link } from 'react-router-dom';
import { PaginationGoods } from './PaginationGoods';

export const GoodsList: React.FC = observer(() => {
  const [type, setType] = useState(null);
  const [device, setDevice] = useState<FieldValues>();
  const [page, setPage] = useState('1');
  const [searchParams, setSearchParams] = useSearchParams();
  const [isLoading, fetching] = useLoading(async () => {
    const response = await useFetching.getAllGoods(type, page);
    if (response.message == 'OK') {
      setDevice(response.device);
    }
  });

  useEffect(() => {
    document.title = `Admin`;
  });

  useEffect(() => {
    setPage(searchParams.get('page') || '1');
    if (!searchParams.get('page')) {
      searchParams.set('page', '1');
      setSearchParams(searchParams);
    }
  }, []);

  useEffect(() => {
    fetching();
  }, [type, page]);

  return (
    <div className="goodslist">
      <div className="goodslist__wrap">
        <div className="goodslist__turn">
          <div className="goodslist__type">
            <p className="">Type</p>
          </div>
          <div className="goodslist__type">
            <p className="">Brand</p>
          </div>
          <div className="goodslist__type">
            <p className="">Title</p>
          </div>
          <div className="goodslist__type">
            <p className="">Price</p>
          </div>
        </div>
        <div className="goodslist__items">
          {isLoading ? (
            <Loading name="all" />
          ) : (
            <>
              {(device?.rows as FieldValues[])?.map((item, index) => {
                return (
                  <GoodsItem
                    key={item.id}
                    item={item}
                    index={index}
                    fetching={fetching}
                  />
                );
              })}
            </>
          )}
        </div>

        {!!device?.count && (
          <PaginationGoods
            count={device.count}
            setPage={setPage}
            pageOld={page}
          />
        )}
      </div>
    </div>
  );
});

export const GoodsItem: React.FC<GoodsType> = observer(
  ({ item, index, fetching }) => {
    const deleteItem = async () => {
      await useFetching.delete(+item.id);
      fetching();
    };
    const router = useNavigate();
    return (
      <div
        className="goodsitem"
        onClick={() => item?.change && router('/admin/item/' + item.id)}
      >
        <div className="goodsitem__wrap">
          <div className="goodsitem__number ">
            <p className="mini">{index + 1}</p>
          </div>
          <div className="goodsitem__type typename">
            <p className="mini">{item?.type?.name}</p>
          </div>
          <div className="goodsitem__type brandname">
            <p className="mini">
              {item?.brand?.name.length <= 10
                ? item.brand.name
                : item.brand.name
                    .split('')
                    .filter((item, index) => index <= 10)
                    .join('') + '...'}
            </p>
          </div>
          <div className="goodsitem__type goodsname">
            <p className="mini">
              {item.name.length <= 10
                ? item.name
                : item.name
                    .split('')
                    .filter((item, index) => index <= 10)
                    .join('') + '...'}
            </p>
          </div>
          <div className="goodsitem__type pricename">
            <p className="mini">${item?.price}</p>
          </div>
          {item.change && (
            <>
              <div className="goodsitem__svg changetype">
                <Link to={'/admin/item/' + item.id}>
                  <Pen />
                </Link>
              </div>
              <div onClick={deleteItem} className="goodsitem__svg deletetype">
                <Basket />
              </div>
            </>
          )}
        </div>
      </div>
    );
  }
);

type GoodsType = {
  item: FieldValues;
  index: number;
  fetching: () => void;
};
