import axios from 'axios';
import { observer } from 'mobx-react-lite';
import { useEffect, useContext, useState } from 'react';
import { FieldValues, SubmitHandler, useForm } from 'react-hook-form';
import { useLocation, useNavigate, useParams } from 'react-router-dom';
import { $ } from '../../../../components/customeJQ/customeJQ';
import useLoading from '../../../../Hooks/useLoading';
import { useFetching } from '../../../../http/shopAPI';
import { Select, SelectFather } from './components/Select/Select';
import { selectedMine } from '../../../Shop/Tuner/components/selectedMine';
import './ChangeGoods.scss';
import Loading from '../../../../UI/Loading/Loading';
import { Context } from '../../../..';
import { TextareItem } from './components/TextareItem/TextareItem';
import { Price } from './components/Price/Price';
import { ProductFileds } from './components/ProductFileds/ProductFileds';

export const ChangeGoods: React.FC = observer(() => {
  const { product } = useContext(Context);
  const location = useLocation().pathname;
  const path = useParams().id;
  const router = useNavigate();
  const [imgOld, setImgOld] = useState('');
  const [itemproduct, setItemProduct] = useState<FieldValues>({});

  const {
    register,
    setValue,
    formState: { errors },
    handleSubmit,
    getValues,
  } = useForm({ mode: 'onSubmit' });

  const [isLoading, fetching] = useLoading(async () => {
    await Promise.all([
      useFetching.getBrands(),
      useFetching.getTypes(),
      useFetching.getColor(),
    ]).then(
      axios.spread((brands, types, colors) => {
        product.setTypes = types;
        product.setBrands = brands;
        product.setColors = colors;
      })
    );

    if (path) {
      const response = await useFetching.getOne(+path);
      setItemProduct(response);
      setValue('category', response.type.id);
      setValue('brand', response.brand.id);
      setValue('color', response.colorId);
      setValue('price', response.price);
      setValue('sale', response.sale);
      setValue('information', response.info);
      setValue('name', response.name);
      setImgOld(response.deviceimg.img);

      product.setOld = true;
    } else {
      setValue('category', '');
      setValue('brand', '');
      setValue('color', '');
      setValue('price', '');
      setValue('sale', '');
      setValue('information', '');
      setValue('name', '');
      setImgOld('');

      product.setOld = false;
    }
  });

  useEffect(() => {
    fetching();
  }, [location]);

  const handelrClick = (
    e: React.MouseEvent<HTMLElement>,
    item?: FieldValues
  ) => {
    e.preventDefault();
    const target = e.target as HTMLElement;
    const li = target.closest('li');
    const block = target.closest('.select') as HTMLElement;
    if (li?.textContent && li) {
      selectedMine(e, 'li');
    } else if (block) {
      $(block).toggle('add');
    }
  };

  const deleteItem = async () => {
    if (path && itemproduct?.change) {
      await useFetching.delete(+path);
      router('/admin/goods?page=1');
    }
  };

  const submit: SubmitHandler<FieldValues> = async (data) => {
    if (path) {
      await useFetching.change(data, +path, imgOld);
      router('/admin/goods?page=1');
    } else {
      const deviceimgId = await useFetching.setImg(data.files);
      if (deviceimgId.message == 'ok') {
        const response = await useFetching.set(data, deviceimgId.body.id);
        router('/admin/goods?page=1');
      }
    }
  };

  return (
    <div className="changegoods">
      <>
        {isLoading ? (
          <Loading name="all" />
        ) : (
          <form onSubmit={handleSubmit(submit)} className="changegoods__form">
            <div className="changegoods__name">
              <div className="changegoods__nameblock">
                <div className="titleproductadmin block ">
                  <h3>Product Name</h3>
                </div>
                <div className="changegoods__nameinput">
                  <input placeholder="Name" {...register('name')} />
                </div>
              </div>
            </div>
            <div className="changegoods__select">
              <SelectFather name="Category">
                <Select
                  name="category"
                  register={register}
                  setValue={setValue}
                  getValues={getValues}
                  handelrClick={handelrClick}
                  array={product.getTypes}
                />
              </SelectFather>
              <SelectFather name="Brand">
                <Select
                  name="brand"
                  getValues={getValues}
                  register={register}
                  setValue={setValue}
                  handelrClick={handelrClick}
                  array={product.getBrands}
                />
              </SelectFather>
              <SelectFather name="Color">
                <Select
                  name="color"
                  getValues={getValues}
                  register={register}
                  setValue={setValue}
                  handelrClick={handelrClick}
                  array={product.getColors}
                />
              </SelectFather>
            </div>
            <div className="changegoods__numbers">
              <Price
                setValue={setValue}
                register={register}
                name="price"
                col={5}
              />
              <Price
                setValue={setValue}
                register={register}
                name="sale"
                col={2}
              />
            </div>

            <TextareItem register={register} name="information" />
            <div className="changegoods__imageblock">
              <ProductFileds
                register={register}
                name={'files'}
                getValues={getValues}
                setValue={setValue}
                setImgOld={setImgOld}
                imgOld={imgOld}
              />
            </div>

            <div className="changegoods__footer">
              {!!path && itemproduct?.change && (
                <button onClick={deleteItem} className="changegoods__delete">
                  <p>DELETE</p>
                </button>
              )}
              {!path ? (
                <button type="submit" className="changegoods__btn">
                  <p>SEND</p>
                </button>
              ) : (
                itemproduct?.change && (
                  <button type="submit" className="changegoods__btn">
                    <p>UPDATE</p>
                  </button>
                )
              )}
            </div>
          </form>
        )}
      </>
    </div>
  );
});
