import { $host } from './index';

export class APIChangeGoods {
  static async setBrandOrtype(name: string, title: string) {
    try {
      const path = title
        .split('')
        .map((item2: string) => {
          return item2.toLowerCase();
        })
        .join('');

      const { data } = await $host.post('api/' + path + '/', {
        name,
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async setColor(name: string, color: string) {
    try {
      const { data } = await $host.post('api/color/', {
        name,
        color,
      });
      return data;
    } catch (error) {
      console.log(error);
    }
  }

  static async delete(id: number, title: string) {
    try {
      const { data } = await $host.delete('api/' + title + '/' + id);
      return data;
    } catch (error) {
      console.log(error);
    }
  }
}
